import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

  const API_URL = 'https://electro-a8bl.onrender.com/api/products';

function ProductForm({ onClose, onSuccess, initialData }) {
  const [name, setName] = useState(initialData?.name || '');
  const [price, setPrice] = useState(initialData?.price !== undefined ? String(initialData.price) : '');
  const [category, setCategory] = useState(initialData?.category || '');
  const [article, setArticle] = useState(initialData?.article || '');
  const [image, setImage] = useState(initialData?.image || '');
  const [photo1, setPhoto1] = useState(initialData?.images?.[0] || '');
  const [photo2, setPhoto2] = useState(initialData?.images?.[1] || '');
  const [photo3, setPhoto3] = useState(initialData?.images?.[2] || '');
  const [description, setDescription] = useState(initialData?.description || '');
  const [shortDescription, setShortDescription] = useState(initialData?.['Short description'] || '');
  const [characteristics, setCharacteristics] = useState(initialData?.characteristics || '');
  const [equipment, setEquipment] = useState(initialData?.equipment || '');
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  //    
  const [characteristicFields, setCharacteristicFields] = useState(() => {
    if (initialData?.characteristics) {
      try {
        //    
        const parsed = JSON.parse(initialData.characteristics);
        if (Array.isArray(parsed)) {
          return parsed;
        }
      } catch {
        //   JSON,   
        const lines = initialData.characteristics.split('\n').filter(line => line.trim());
        return lines.map(line => {
          const parts = line.split(':');
          if (parts.length >= 2) {
            return {
              parameter: parts[0].trim(),
              value: parts.slice(1).join(':').trim()
            };
          }
          return { parameter: line.trim(), value: '' };
        });
      }
    }
    //  
    return [
      { parameter: ' ', value: '' },
      { parameter: ' ', value: '' },
      { parameter: ' ', value: '' },
      { parameter: '  ', value: '' },
      { parameter: ' ', value: '' },
      { parameter: '    ', value: '' },
      { parameter: '  ', value: '' },
      { parameter: ' ', value: '' },
      { parameter: '   ', value: '' },
      { parameter: '   ', value: '' }
    ];
  });
  
  //     
  const handleFileUpload = async (event, setField) => {
    const files = Array.from(event.target.files);
    if (files.length === 0) return;
    
    setLoading(true);
    setError('');
    
    try {
      const formData = new FormData();
      formData.append('file', files[0]);
      
      const response = await fetch('/upload.php', {
        method: 'POST',
        body: formData
      });
      
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.error || '  ');
      }
      
      const result = await response.json();
      
      if (result.success && result.files.length > 0) {
        const newUrl = result.files[0];
        setField(newUrl);
        //    
        setError('');
        setTimeout(() => {
          alert(`   !\n\nURL: ${newUrl}\n\nURL    .`);
        }, 100);
      } else {
        setError('  ');
      }
    } catch (err) {
      setError('  : ' + err.message);
    } finally {
      setLoading(false);
      event.target.value = '';
    }
  };

  //     
  const addCharacteristic = () => {
    setCharacteristicFields([...characteristicFields, { parameter: '', value: '' }]);
  };

  const removeCharacteristic = (index) => {
    setCharacteristicFields(characteristicFields.filter((_, i) => i !== index));
  };

  const updateCharacteristic = (index, field, value) => {
    const newFields = [...characteristicFields];
    newFields[index][field] = value;
    setCharacteristicFields(newFields);
  };

  //      
  const formatCharacteristics = () => {
    return characteristicFields
      .filter(field => field.parameter.trim() && field.value.trim())
      .map(field => `${field.parameter}: ${field.value}`)
      .join('\n');
  };

  const isEdit = Boolean(initialData && initialData._id);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError('');
    
    //       
    let parsedPrice = String(price).replace(',', '.');
    if (parsedPrice === '' || isNaN(Number(parsedPrice))) {
      setError('   (: 19.65  19,65)');
      setLoading(false);
      return;
    }
    parsedPrice = Number(parsedPrice);
    
    //     
    const allPhotos = [photo1, photo2, photo3].filter(photo => photo.trim() !== '');
    
    try {
      let payload = { 
        name, 
        price: parsedPrice, 
        category, 
        image, 
        images: allPhotos,
        images2: [],
        images3: [],
        description, 
        'Short description': shortDescription, 
        characteristics: formatCharacteristics(), 
        equipment, 
        article 
      };
      
      const res = await fetch(isEdit ? `${API_URL}/${initialData._id}` : API_URL, {
        method: isEdit ? 'PUT' : 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(payload)
      });
      
      if (!res.ok) throw new Error(isEdit ? '   ' : '   ');
      
      setLoading(false);
      onSuccess();
      onClose();
    } catch (err) {
      setError(err.message || '');
      setLoading(false);
    }
  };

  return (
    <div style={{position:'fixed',top:0,left:0,right:0,bottom:0,background:'rgba(0,0,0,0.18)',zIndex:1000,display:'flex',alignItems:'center',justifyContent:'center'}}>
      <form onSubmit={handleSubmit} style={{background:'#fff',borderRadius:10,padding:28,minWidth:400,maxWidth:600,boxShadow:'0 2px 16px rgba(30,40,90,0.10)',maxHeight:'90vh',overflowY:'auto'}}>
        <h3 style={{marginTop:0,marginBottom:20,fontWeight:700,fontSize:22,color:'#333'}}>{isEdit ? ' ' : ' '}</h3>
        
        {/*   */}
        <div style={{background:'#f8f9fa',border:'1px solid #e9ecef',borderRadius:8,padding:16,marginBottom:20}}>
          <h4 style={{margin:'0 0 12px 0',fontSize:16,fontWeight:600,color:'#495057'}}>  </h4>
          
          <div style={{marginBottom:12}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}>  *</label>
            <input required value={name} onChange={e=>setName(e.target.value)} placeholder="  " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
          </div>
          
          <div style={{marginBottom:12}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}> *</label>
            <input required type="text" value={price} onChange={e=>setPrice(e.target.value)} placeholder=": 19.65  19,65" style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
          </div>
          
          <div style={{marginBottom:12}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}></label>
            <input value={category} onChange={e=>setCategory(e.target.value)} placeholder=": " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
          </div>
          
          <div style={{marginBottom:0}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}></label>
            <input value={article} onChange={e=>setArticle(e.target.value)} placeholder=": 119356208" style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
            <small style={{color:'#6c757d',fontSize:12}}>  </small>
          </div>
        </div>

        {/*  */}
        <div style={{background:'#f8f9fa',border:'1px solid #e9ecef',borderRadius:8,padding:16,marginBottom:20}}>
          <h4 style={{margin:'0 0 12px 0',fontSize:16,fontWeight:600,color:'#495057'}}> </h4>
          
          <div style={{marginBottom:12}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}>  *</label>
            <input required value={image} onChange={e=>setImage(e.target.value)} placeholder="URL  " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
            <div style={{display:'flex',gap:8,marginTop:6}}>
              <input type="file" accept="image/*" onChange={(e)=>handleFileUpload(e, setImage)} style={{flex:1}} />
              <button type="button" onClick={()=>setImage('/images/products/bolgarka-makita-125.jpg')} style={{background:'#28a745',color:'#fff',border:'none',borderRadius:4,padding:'8px 12px',fontSize:12,cursor:'pointer'}}></button>
            </div>
          </div>
          
          <div style={{marginBottom:12}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}> 2</label>
            <input value={photo1} onChange={e=>setPhoto1(e.target.value)} placeholder="URL  " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
            <div style={{display:'flex',gap:8,marginTop:6}}>
              <input type="file" accept="image/*" onChange={(e)=>handleFileUpload(e, setPhoto1)} style={{flex:1}} />
              <button type="button" onClick={()=>setPhoto1('/images/products/drel.jpg')} style={{background:'#28a745',color:'#fff',border:'none',borderRadius:4,padding:'8px 12px',fontSize:12,cursor:'pointer'}}></button>
            </div>
          </div>
          
          <div style={{marginBottom:12}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}> 3</label>
            <input value={photo2} onChange={e=>setPhoto2(e.target.value)} placeholder="URL  " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
            <div style={{display:'flex',gap:8,marginTop:6}}>
              <input type="file" accept="image/*" onChange={(e)=>handleFileUpload(e, setPhoto2)} style={{flex:1}} />
              <button type="button" onClick={()=>setPhoto2('/images/products/perforator-bosch-gbh.jpg')} style={{background:'#28a745',color:'#fff',border:'none',borderRadius:4,padding:'8px 12px',fontSize:12,cursor:'pointer'}}></button>
            </div>
          </div>
          
          <div style={{marginBottom:0}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}> 4</label>
            <input value={photo3} onChange={e=>setPhoto3(e.target.value)} placeholder="URL  " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14}} />
            <div style={{display:'flex',gap:8,marginTop:6}}>
              <input type="file" accept="image/*" onChange={(e)=>handleFileUpload(e, setPhoto3)} style={{flex:1}} />
              <button type="button" onClick={()=>setPhoto3('/images/products/shurupovert-dewalt-18v.jpg')} style={{background:'#28a745',color:'#fff',border:'none',borderRadius:4,padding:'8px 12px',fontSize:12,cursor:'pointer'}}></button>
            </div>
          </div>
        </div>

        {/*  */}
        <div style={{background:'#f8f9fa',border:'1px solid #e9ecef',borderRadius:8,padding:16,marginBottom:20}}>
          <h4 style={{margin:'0 0 12px 0',fontSize:16,fontWeight:600,color:'#495057'}}> </h4>
          
          <div style={{marginBottom:12}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}> </label>
            <textarea value={shortDescription} onChange={e=>setShortDescription(e.target.value)} placeholder="   ( 160 )" maxLength={160} style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14,minHeight:60,resize:'vertical'}} />
            <small style={{color:'#6c757d',fontSize:12}}>   </small>
          </div>
          
          <div style={{marginBottom:0}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}> </label>
            <textarea value={description} onChange={e=>setDescription(e.target.value)} placeholder="  ,    " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14,minHeight:80,resize:'vertical'}} />
          </div>
        </div>

        {/*    */}
        <div style={{background:'#f8f9fa',border:'1px solid #e9ecef',borderRadius:8,padding:16,marginBottom:20}}>
          <h4 style={{margin:'0 0 12px 0',fontSize:16,fontWeight:600,color:'#495057'}}>   </h4>
          
          <div style={{marginBottom:16}}>
            <div style={{display:'flex',justifyContent:'space-between',alignItems:'center',marginBottom:12}}>
              <label style={{fontWeight:500,color:'#333',fontSize:14}}> </label>
              <button type="button" onClick={addCharacteristic} style={{background:'#28a745',color:'#fff',border:'none',borderRadius:4,padding:'6px 12px',fontSize:12,cursor:'pointer'}}>+ </button>
            </div>
            
            <div style={{maxHeight:300,overflowY:'auto',border:'1px solid #ced4da',borderRadius:6,padding:8,background:'#fff'}}>
              {characteristicFields.map((field, index) => (
                <div key={index} style={{display:'flex',gap:8,marginBottom:8,alignItems:'center'}}>
                  <input
                    value={field.parameter}
                    onChange={(e) => updateCharacteristic(index, 'parameter', e.target.value)}
                    placeholder=" (:  )"
                    style={{flex:1,padding:8,borderRadius:4,border:'1px solid #ced4da',fontSize:13}}
                  />
                  <input
                    value={field.value}
                    onChange={(e) => updateCharacteristic(index, 'value', e.target.value)}
                    placeholder=" (: 119356208)"
                    style={{flex:1,padding:8,borderRadius:4,border:'1px solid #ced4da',fontSize:13}}
                  />
                  <button
                    type="button"
                    onClick={() => removeCharacteristic(index)}
                    style={{background:'#dc3545',color:'#fff',border:'none',borderRadius:4,padding:'6px 8px',fontSize:12,cursor:'pointer',minWidth:30}}
                  >
                    
                  </button>
                </div>
              ))}
            </div>
            
            <small style={{color:'#6c757d',fontSize:12,marginTop:8,display:'block'}}>
                    ": ". 
                 .
            </small>
          </div>
          
          <div style={{marginBottom:0}}>
            <label style={{display:'block',marginBottom:4,fontWeight:500,color:'#333',fontSize:14}}></label>
            <textarea value={equipment} onChange={e=>setEquipment(e.target.value)} placeholder="  &#10;  &#10;    &#10;   " style={{width:'100%',padding:10,borderRadius:6,border:'1px solid #ced4da',fontSize:14,minHeight:80,resize:'vertical'}} />
            <small style={{color:'#6c757d',fontSize:12}}>    </small>
          </div>
        </div>

        {error && <div style={{color:'#dc3545',marginBottom:16,padding:12,background:'#f8d7da',border:'1px solid #f5c6cb',borderRadius:6}}>{error}</div>}
        
        <div style={{display:'flex',justifyContent:'flex-end',gap:12}}>
          <button type="button" onClick={onClose} style={{background:'#6c757d',color:'#fff',border:'none',borderRadius:6,padding:'10px 20px',fontWeight:500,cursor:'pointer',fontSize:14}}></button>
          <button type="submit" disabled={loading} style={{background:'#FF6B00',color:'#fff',border:'none',borderRadius:6,padding:'10px 20px',fontWeight:600,cursor:'pointer',fontSize:14}}>{loading ? (isEdit ? '...' : '...') : (isEdit ? '' : ' ')}</button>
        </div>
      </form>
    </div>
  );
}

const ProductList = ({ onLogout }) => {
  const [products, setProducts] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const [showForm, setShowForm] = useState(false);
  const [editProduct, setEditProduct] = useState(null);

  const navigate = useNavigate();

  const fetchProducts = () => {
    setLoading(true);
    fetch(API_URL)
      .then(res => res.json())
      .then(data => {
        setProducts(Array.isArray(data) ? data : []);
        setLoading(false);
      })
      .catch(() => {
        setError('  ');
        setLoading(false);
      });
  };

  useEffect(() => {
    fetchProducts();
  }, []);

  const handleEdit = (product) => {
    setEditProduct(product);
    setShowForm(true);
  };

  const handleFormClose = () => {
    setShowForm(false);
    setEditProduct(null);
  };

  const handleDelete = async (product) => {
    if (!window.confirm(`  ${product.name}?`)) return;
    try {
      const res = await fetch(`${API_URL}/${product._id}`, { method: 'DELETE' });
      if (!res.ok) throw new Error('   ');
      fetchProducts();
    } catch (e) {
      alert('   ');
    }
  };

  return (
    <div style={{minHeight: '100vh', background: '#f5f7fa', padding: '32px 0'}}>
      <div style={{maxWidth: 1100, margin: '0 auto', background: '#fff', borderRadius: 10, border: '1.5px solid #e0e0e0', padding: 24}}>
        <div style={{display: 'flex', justifyContent: 'space-between', alignItems: 'center', marginBottom: 18}}>
          <h2 style={{fontWeight: 700, fontSize: 24, color: '#1a2236', margin: 0}}></h2>
          <div>
            <button onClick={()=>{setShowForm(true);setEditProduct(null);}} style={{background: '#FF6B00', color: '#fff', fontWeight: 600, fontSize: 15, border: 'none', borderRadius: 7, padding: '8px 18px', marginRight: 12, cursor: 'pointer'}}>+  </button>
            <button onClick={() => navigate('/admin/settings')} style={{background: '#1e88e5', color: '#fff', fontWeight: 600, fontSize: 15, border: 'none', borderRadius: 7, padding: '8px 18px', marginRight: 12, cursor: 'pointer'}}> </button>
            <button onClick={onLogout} style={{background: '#e53935', color: '#fff', fontWeight: 600, fontSize: 15, border: 'none', borderRadius: 7, padding: '8px 18px', cursor: 'pointer'}}></button>
          </div>
        </div>
      {loading ? (
          <div style={{padding: 32, textAlign: 'center'}}>...</div>
      ) : error ? (
          <div style={{color: '#e53935', padding: 32, textAlign: 'center'}}>{error}</div>
      ) : (
          <table style={{width: '100%', borderCollapse: 'collapse', fontSize: 15, background: '#fff'}}>
          <thead>
            <tr style={{background: '#f5f7fa'}}>
                <th style={{padding: '8px 6px', textAlign: 'left', fontWeight: 600, color: '#222'}}></th>
                <th style={{padding: '8px 6px', textAlign: 'left', fontWeight: 600, color: '#222'}}></th>
                <th style={{padding: '8px 6px', textAlign: 'left', fontWeight: 600, color: '#222'}}></th>
                <th style={{padding: '8px 6px', textAlign: 'left', fontWeight: 600, color: '#222'}}></th>
                <th style={{padding: '8px 6px', textAlign: 'left', fontWeight: 600, color: '#222'}}> </th>
                <th style={{padding: '8px 6px', textAlign: 'center', fontWeight: 600, color: '#222'}}></th>
            </tr>
          </thead>
          <tbody>
            {products.map(product => (
                <tr key={product._id} style={{borderBottom: '1px solid #e0e0e0'}}>
                  <td style={{padding: '6px 6px'}}>
                    <img src={product.image || '/images/products/placeholder.png'} alt={product.name} style={{width: 44, height: 44, objectFit: 'contain', borderRadius: 5, background: '#f5f7fa', border: '1px solid #e0e0e0'}} />
                </td>
                  <td style={{padding: '6px 6px', fontWeight: 500, color: '#1a2236'}}>{product.name}</td>
                  <td style={{padding: '6px 6px', color: '#FFB300', fontWeight: 700}}>{product.price ? Number(product.price).toLocaleString('ru-RU') + ' ' : ''}</td>
                  <td style={{padding: '6px 6px', color: '#222'}}>{product.category || '-'}</td>
                  <td style={{padding: '6px 6px', color: '#888', fontSize: 13}}>{product['Short description'] || ''}</td>
                  <td style={{padding: '6px 6px', textAlign: 'center'}}>
                    <button onClick={()=>handleEdit(product)} style={{background: '#1e88e5', color: '#fff', border: 'none', borderRadius: 6, padding: '6px 14px', fontWeight: 500, marginRight: 6, cursor: 'pointer'}}></button>
                    <button onClick={()=>handleDelete(product)} style={{background: '#e53935', color: '#fff', border: 'none', borderRadius: 6, padding: '6px 14px', fontWeight: 500, cursor: 'pointer'}}></button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
      </div>
      {showForm && <ProductForm onClose={handleFormClose} onSuccess={fetchProducts} initialData={editProduct} />}
    </div>
  );
};

export default ProductList; 